export const urlRegex = /^https?:\/\/(([%a-zA-Z0-9_-])+(\.)?)*(:\d+)?(\/((\.)?(\?)?=?&?[%a-zA-Z0-9_-](\?)?)*)*$/i;
const urlRegex2 = /^\/api/;
const { fetch, encodeURIComponent } = window;
// const memory = new Memory('session');
// export const host = 'https://gcxt.mingyuanyun.com';
// export const host = "https://gcxt-test.myfuwu.com.cn";
let host = process.env.NODE_ENV === 'production' ? window.location.origin : '/api';
export { host };

let LM = encodeURIComponent('['),
		RM = encodeURIComponent(']');
// 改版的序列化，待观察
const serialize2 = (target, prefix = '') => {
	if((target === null || target === void 0) && prefix){
		// 非法值传空
		return `${prefix}=`;
	}else if(target instanceof Array){
		if(target.length === 0){
			return serialize2('[]', prefix);
		}
		let isRelativeArray = target.some(item => item instanceof Object);

		if(isRelativeArray){
			// 关联数组
			return target.map((item, index) => 
				serialize2(item, `${prefix}${LM}${index}${RM}`)
			).join('&');
		}else{
			// 索引数组
			return target.map((item, index) => 
				`${prefix}${LM}${RM}=${item}`
			).join('&');
		}
	}else if(target instanceof Object){
		return Object.keys(target).map(key => {
			let value = target[key];
			let _prefix = prefix ? `${prefix}${LM}${key}${RM}` : key;
			
			return serialize2(value, _prefix);
		}).join('&');
	}else if(prefix){
		// 简单值
		let _value = encodeURIComponent(target);
		return `${prefix}=${_value}`
	}
}

export const handleQueryString = (location = '', params) => {
	const qs = serialize2(params);
	let connector = location.indexOf('?') < 0 ? '?' : '&';
	return ( [location, qs] ).join(connector);
}

const defaultPostSetting = {
	method: 'POST',
	headers: {
		'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8',
	},
	body: {
		user_id: '',
		__tenant_id: '',
		cur_role_code: '',
		access_token: '',
		__from: 'web',
	}
};

// set information into http request body as default params,
// this is useful after login.
export const fetchPostSetting = sets => {
	Object.assign(defaultPostSetting.body, sets);
};
// handling http request parameter
export const handleFetchPost = (raw = {}) => {
	let body = Object.assign(
		{}, 
		defaultPostSetting.body, 
		raw.body
	);

	const setting = Object.assign(
		{}, 
		defaultPostSetting,
		raw,
		{
			body: serialize2(body)
		}
	)
	return setting;
};

// 封装了fetch方法
// 默认修改了option，用post请求
/**
 * 
 * @param {string} target - 请求地址
 * @param {Object} option - 请求配置，将用post表单提交，具体参看 handleFetchPost
 * option 中特殊的选项
 *
 * @returns {Promise} promise
 */ 
export const _fetch = (target = '/', option = {}) => {
	if(!host){
		host = window.location.origin;
	}
	if(!urlRegex.test(target) && !urlRegex2.test(target)){
		target = host + target;
	}

	return fetch(target, handleFetchPost(option))
		.then(response => {
			if(200 <= response.status && response.status < 300){
				return response;
			}else{
				const error = new Error(response.statusText)
				error.response = response;
				throw error
			}
		})
		.catch(error => {
			throw error
		})
		.then(res => res.json())
		.then(res => {
			// handle success sign
			if(res.success === 1 || res.success === true){
				return res;
			}else{
				const error = new Error(res.message);
				error.response = res.code;
				error.data = res.data;
				throw error;
			}
		})
};



